@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@100&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Trebuchet MS', 'Times New Roman', Times, sans-serif;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

#root {
  position: relative;
  height: 100vh;
}

.unstyled-link {
  text-decoration: none;
  color: black;
}

.header {
  position: relative;
  background-color: rgb(175, 73, 73);
  padding-left: 2rem;
  padding-right: 2rem;
  text-align: center;
  z-index: 10;
}

.header-logo {
  margin: 0;

}

.navbar-list {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.navbar-item {
  cursor: pointer;
  padding: 1rem;
  transition: all 0.2s linear;
}

.navbar-item:hover {
  background-color: rgba(255, 255, 255, 0.7);
}

.level-one-container {
  display: flex;
  justify-content: center;
  margin: 2rem;
}

.level-one-link {
  padding: 2rem;
  text-align: center;
  border: 4px dashed rgb(99, 96, 96);
  border-radius: 5px;
}

.level-one-link:hover {
  background-color: rgb(99, 96, 96, 0.3);;
}

.active {
  border-bottom: 4px solid orangered;
  background-color: rgba(255, 255, 255, 0.7);
}
.cat-eat-cards{display:flex;position:relative;justify-content:center;flex-wrap:wrap;max-width:1200px}

.cat-eat-wrapper{width:320px;margin-left:40px;margin-right:40px;margin-bottom:65px}.cat-eat-wrapper .cat-eat-card{position:relative;cursor:pointer;max-height:480px;min-height:480px;color:#000000;-webkit-clip-path:polygon(100% 0%, 100% 100%, 0 100%, 0 8.5%, 12.5% 0);clip-path:polygon(100% 0%, 100% 100%, 0 100%, 0 8.5%, 12.5% 0);background-color:#f2f2f2;border:4px solid #1698d9;border-radius:10px;background-image:url(/funbox/static/media/cat.3ad87d7b.png);background-repeat:no-repeat;background-position:-24px 205px;padding:12px;transition:all 0.2s linear}.cat-eat-wrapper .cat-eat-card__card-header{font-size:16px;color:#666;margin-top:9px;margin-left:33px}.cat-eat-wrapper .cat-eat-card__card-body{margin-left:33px}.cat-eat-wrapper .cat-eat-card__card-body__title{font-size:48px;margin-top:17px;margin-bottom:0}.cat-eat-wrapper .cat-eat-card__card-body__with{font-size:24px}.cat-eat-wrapper .cat-eat-card__card-body__promo{margin-top:15px;color:#666;font-size:14px}.cat-eat-wrapper .cat-eat-card__weight{position:absolute;text-align:center;color:#ffffff;background-color:#1698d9;width:82px;height:82px;font-size:40px;right:12px;bottom:12px;border-radius:50%;padding-top:8px;transition:all 0.2s linear}.cat-eat-wrapper .cat-eat-card__weight .unit{font-size:15px}.cat-eat-wrapper .cat-eat-card::before{content:"";display:block;position:absolute;width:60px;height:4px;background-color:#1698d9;transform:rotate(-45deg);left:-10px;transition:all 0.2s linear}.cat-eat-wrapper .cat-eat-card:hover{border-color:#66c1ef}.cat-eat-wrapper .cat-eat-card:hover .cat-eat-card__weight,.cat-eat-wrapper .cat-eat-card:hover::before{background-color:#66c1ef}.cat-eat-wrapper .cat-eat-footer{text-align:center;margin-top:15px}.cat-eat-wrapper .cat-eat-footer__link{text-decoration:none;color:#1698d9;border-bottom:2px dashed #1698d9;transition:all 0.2s linear}.cat-eat-wrapper .cat-eat-footer__link:hover{color:#66c1ef;border-color:#66c1ef}.cat-eat-wrapper .selected{border:4px solid #d91667}.cat-eat-wrapper .selected::before{content:"";display:block;position:absolute;width:60px;height:4px;background-color:#d91667;transform:rotate(-45deg);left:-10px;transition:all 0.2s linear}.cat-eat-wrapper .selected:hover{border-color:#ef669f}.cat-eat-wrapper .selected:hover .cat-eat-card__weight,.cat-eat-wrapper .selected:hover::before{background-color:#ef669f}.cat-eat-wrapper .selected .cat-eat-card__weight{background-color:#d91667}.cat-eat-wrapper .disabled{position:relative;border:4px solid #b3b3b3}.cat-eat-wrapper .disabled::before{content:"";display:block;position:absolute;width:60px;height:4px;background-color:#b3b3b3;transform:rotate(-45deg);left:-10px;transition:all 0.2s linear;z-index:10}.cat-eat-wrapper .disabled::after{content:"";cursor:default;display:block;position:absolute;border-radius:10px;top:0;bottom:0;left:0;right:0;background-color:rgba(255,255,255,0.6)}.cat-eat-wrapper .disabled .cat-eat-card__weight{background-color:#b3b3b3}.cat-eat-wrapper .disabled:hover{border:4px solid #b3b3b3}.cat-eat-wrapper .disabled:hover .cat-eat-card__weight,.cat-eat-wrapper .disabled:hover::before{background-color:#b3b3b3}@media screen and (max-width: 400px){.cat-eat-wrapper{margin-left:0;margin-right:0}}

.cat-eat-container{position:relative;display:flex;justify-content:center;align-items:flex-start;flex-wrap:wrap;color:white}.cat-eat-title{display:block;width:100%;font-family:'Exo 2.0', sans-serif;font-weight:100;font-size:36px;text-align:center;margin-top:47px;margin-bottom:20px}

.container-wrapper{display:flex;justify-content:center;align-items:center;background-color:#000;background-image:url(/funbox/static/media/bg.da7154cc.png)}.container-wrapper::after{content:"";position:absolute;display:block;background-color:#000;background-image:url(/funbox/static/media/bg.da7154cc.png);top:0;right:0;bottom:0;left:0;height:100vh;z-index:-100}

